.blockquote{
  background-color: #f9f9f9;
  padding-top: 85px;
  padding-bottom: 75px;
  &__p{
    color: #000000;
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;    
    line-height: 28px;
    margin-bottom: 18px;
    &--strong{
      font-family: 'Roboto', sans-serif; 
      font-weight: 500;      
    }
  }
  a{
    display: inline-block;
    color: #68c4c7;
    background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/100% 1Px; 
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    line-height: 28px;
    color: #68c4c7;
    transition: 0.3s;
    &:hover{
      background-size: 0 1px;
    }
  }
  @include media-breakpoint-up(sm) {
    &__p{
      margin-bottom: 0;
      &--strong{ 
        margin-bottom: 0;    
      }
    }
  }
}