.promo{
  height: 350px;
  overflow: hidden;
  margin-bottom: 50px;
  position: relative;
  z-index: 1;
  &__img{
    position: absolute;
    right: 0;
    top: 0;
  }
  &__content{
    position: absolute;
    bottom: auto;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    z-index: 10;
    -webkit-user-select: none;
  }
  &__title{
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.4;
    display: inline-block;
    max-width: 320px;
    margin-bottom: 20px;
    -webkit-user-select: none;
  }  
  &__text{
    color: #ffffff; 
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    max-width: 320px;
    font-size: 16px; 
    -webkit-user-select: none;   
  }


  @include media-breakpoint-up(xss) {
    height: auto;

    &__img{
      width: 100%;
      position: static;
    }    

  }


  @include media-breakpoint-up(sm) {
    margin-bottom: 100px;    
    &__content{
      bottom: auto;
      top: 50%;
      transform: translateY(-50%);
    }  

    &__title{
      font-size: 30px;
      line-height: 40px;
      margin-bottom: 30px;
      max-width: 350px;
    }  
  } 
  @include media-breakpoint-up(lg) {
    background: #040d14;
    height: 100vh;    
  }


}