.content {
  color: $color-base;
  text-align: left;
  display: block;

  p, .p {
    --scale: 1;  
    font-size: calc-size(18);  
    color: #000000;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    line-height: 28px;
  }
  a{
    display: inline-block;
    color: #68c4c7;
    background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/100% 1Px; 
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 300;
    line-height: 28px;
    color: #68c4c7;
    transition: 0.3s;
    &:hover{
      background-size: 0 1px;
    }
  }
  
  h1, .h1,
  h2, .h2,
  h3, .h3,
  h4, .h4,
  h5, .h5,
  h6, .h6 {
    font-weight: 500;
    line-height: 1.5;
    margin: 0 0 1em;

  }


  h1, .h1 {
    color: #000000;
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
    font-weight: 500;
  }

  h2, .h2 {
    font-size: calc-size(30);
  }

  h3, .h3 {
    font-size: calc-size(24);
  }

  h4, .h4 {
    font-size: calc-size(18);
  }

  h5, .h5 {
    font-size: calc-size(16);
  }

  h6, .h6 {
    font-size: calc-size(14);
  }

  @include media-breakpoint-up(sm) {

  }
}