.page {
  color: #fff;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
  background: #ffffff;
}

.page__body-inner {
  overflow: hidden;
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

.page__header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.page__overlay {
  height: 100%;
  left: 0;
  opacity: 1;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  background: black;
  display: none;
}

@media (max-width: 1199.98px) {
  .page__body--header-open {
    overflow: hidden;
  }
}

@media (max-width: 799.98px) {
  .page__body--header-open .page__overlay {
    display: block;
  }
}

.header {
  color: #fff;
  background-color: transparent;
  height: 70px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 10001;
}

.header__logo {
  margin-top: 2px;
}

.header .hamburger {
  margin-left: -15px;
  padding: 15px 0;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0 25px 0 0;
  overflow: visible;
}

.header .hamburger:hover {
  opacity: 0.7;
}

.header .hamburger-box {
  width: 40px;
  height: 17px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.header .hamburger-inner {
  display: block;
}

.header .hamburger-inner, .header .hamburger-inner::after {
  width: 40px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  top: 0;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.header .hamburger-inner::before, .header .hamburger-inner::after {
  content: "";
  display: block;
}

.header .hamburger-inner::before {
  top: -10px;
}

.header .hamburger-inner::after {
  width: 26px;
  bottom: -10px;
}

.header .hamburger--elastic .hamburger-inner {
  transition-duration: 0.275s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.header .hamburger--elastic .hamburger-inner::before {
  top: 10px;
  transition: opacity 0.125s 0.275s ease;
}

.header .hamburger--elastic .hamburger-inner::after {
  top: 10px;
  transition: transform 0.275s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.header.header--open .hamburger--elastic .hamburger-inner {
  transform: translate3d(0, 10px, 0) rotate(135deg);
  transition-delay: 0.075s;
}

.header.header--open .hamburger--elastic .hamburger-inner::before {
  transition-delay: 0s;
  opacity: 0;
}

.header.header--open .hamburger--elastic .hamburger-inner::after {
  transform: translate3d(0, -20px, 0) rotate(-270deg);
  transition-delay: 0.075s;
  width: 40px;
  top: 19px;
}

.header__inner {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  padding-left: 0;
  padding-right: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}

@media (min-width: 800px) {
  .header .hamburger {
    display: none;
  }
}

@media (min-width: 1200px) {
  .header__inner {
    padding-left: 0;
    padding-right: 0;
  }
}

.menu {
  width: 100%;
  display: none;
}

.menu ul {
  font-family: 'Roboto', sans-serif;
  font-effect: 400px;
  font-style: normal;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  text-align: right;
}

.menu__item {
  display: inline-block;
  vertical-align: middle;
  margin-left: 48px;
  padding: 23px 0;
  background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/0 2Px;
  transition: background 0.3s;
}

.menu__item:first-child {
  margin-left: 0;
}

.menu__item.active {
  background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/100% 2Px;
}

.menu__item.active .menu__link {
  opacity: 1;
}

.menu__item:hover {
  background-size: 100% 2px;
}

.menu__link {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  opacity: 0.4;
  font-size: 14px;
  line-height: 17px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 3px 0;
  transition: 0.3s;
}

.menu__link:hover {
  opacity: 1;
}

@media (min-width: 800px) {
  .menu {
    display: block;
  }
}

@media (max-width: 799.98px) {
  .page__body--header-open .menu {
    display: block;
    position: absolute;
    width: 100%;
    top: 100%;
    margin-top: 70px;
    left: 0;
    right: 0;
  }
  .page__body--header-open .menu ul {
    flex-direction: column;
  }
  .page__body--header-open .menu__item {
    display: block;
    margin-left: 0;
    padding: 0;
    margin-bottom: 25px;
  }
}

.footer {
  background-color: #fff;
  padding: 0 15px 40px 15px;
  text-align: center;
}

.footer__logo {
