}

.lang__item:hover {
  background-size: 100% 2px;
}

.lang__link {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  opacity: 0.4;
  font-size: 16px;
  line-height: 17px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
