.promo {
  height: 350px;
  overflow: hidden;
  margin-bottom: 50px;
  position: relative;
  z-index: 1;
}

.promo__img {
  position: absolute;
  right: 0;
  top: 0;
}

.promo__content {
  position: absolute;
  bottom: auto;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  z-index: 10;
  -webkit-user-select: none;
}

.promo__title {
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.4;
  display: inline-block;
  max-width: 320px;
  margin-bottom: 20px;
  -webkit-user-select: none;
}

.promo__text {
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  max-width: 320px;
  font-size: 16px;
  -webkit-user-select: none;
}

@media (min-width: 600px) {
  .promo {
    height: auto;
  }
  .promo__img {
    width: 100%;
    position: static;
  }
}

@media (min-width: 800px) {
  .promo {
    margin-bottom: 100px;
  }
  .promo__content {
    bottom: auto;
    top: 50%;
    transform: translateY(-50%);
  }
  .promo__title {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 30px;
    max-width: 350px;
  }
}

@media (min-width: 1200px) {
  .promo {
    background: #040d14;
    height: 100vh;
  }
}

.content {
  color: #fff;
  text-align: left;
  display: block;
}

.content p, .content .p {
  --scale: 1;
  font-size: calc(18px*var(--scale));
  color: #000000;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 28px;
}

.content a {
  display: inline-block;
  color: #68c4c7;
  background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/100% 1Px;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 28px;
  color: #68c4c7;
  transition: 0.3s;
}

.content a:hover {
  background-size: 0 1px;
}

.content h1, .content .h1,
.content h2, .content .h2,
.content h3, .content .h3,
.content h4, .content .h4,
.content h5, .content .h5,
.content h6, .content .h6 {
  font-weight: 500;
  line-height: 1.5;
  margin: 0 0 1em;
}

.content h1, .content .h1 {
  color: #000000;
  font-family: 'Roboto', sans-serif;
  font-size: 30px;
  font-weight: 500;
}

.content h2, .content .h2 {
  font-size: calc(30px*var(--scale));
}

.content h3, .content .h3 {
  font-size: calc(24px*var(--scale));
}

.content h4, .content .h4 {
  font-size: calc(18px*var(--scale));
}

.content h5, .content .h5 {
  font-size: calc(16px*var(--scale));
}

.content h6, .content .h6 {
  font-size: calc(14px*var(--scale));
}

.table {
  padding-top: 0;
}

.table__item:not(.footable-details) {
  width: 100%;
  max-width: 450px;
}

.table__item:not(.footable-details) thead,
.table__item:not(.footable-details) tbody {
  padding-left: 70px;
  padding-right: 70px;
}

.table__item:not(.footable-details) span {
  color: #000000;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 20px;
  display: block;
  transition: color 0.3s;
}

.table__item:not(.footable-details) th p {
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
}

.table__item:not(.footable-details) tbody tr {
