.btn-up {
  transform: rotate(-90deg);
  display: inline-block;
  position: relative;
}

.btn-up__arrow {
  color: #000000;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 28px;
  margin-top: -50px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  width: 60px;
  transition: color 0.3s;
}

.btn-up__arrow:after {
  content: '';
  display: inline-block;
  margin-left: 6px;
  width: 15px;
  height: 15px;
  border: 2px solid #000;
  border-width: 2px 2px 0 0;
  transform: rotate(45deg);
  position: absolute;
  bottom: 50%;
  margin-bottom: -7.5px;
  right: -18px;
  transition: border-color 0.3s;
}

.btn-up__arrow:hover {
  color: #68c4c7;
}

.btn-up__arrow:hover:after {
  border-color: #68c4c7;
}

.nav-slider {
