.lang {
  display: none;
  align-items: center;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  &__item{
    display: inline-block;
    vertical-align: middle;
    padding: 23px 0;
    margin-left: 20px;
    background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/0 2Px;
    transition: background 0.3s; 
    &.active{
    	background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/100% 2Px; 
    	.lang__link {
    		opacity: 1;
    	}
    }
    &:hover{
      background-size: 100% 2px;
    }
  }
  &__link {
    color: $color-base;
    text-decoration: none;
    text-transform: uppercase;
    opacity: 0.4;
    font-size: 16px;
    line-height: 17px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 3px 0;
    transition: 0.3s;

    &:hover {
      opacity: 1;
    }  
  }


  @include media-breakpoint-up(xss) {
    display: flex;
  }
}

@include media-breakpoint-down(xss) {
  .page__body--header-open {
    .lang {
      display: flex; 
      position: absolute;
      top: 100%;
      right: 15px; 
      height: auto;  
      &__item{
        padding: 5px 0;

      }
    }
  }
}