.page {
  color: $color-base;
  font-weight: $font-weight-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
  font-family: $font-family-base;
  background: $color-base-bg;

  &__body-inner {
    overflow: hidden;
    display: flex;
    flex-flow: column;
    min-height: 100vh;
  }

  &__header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: $zindex-fixed;
  }
  &__overlay{
    height: 100%;
    left: 0;
    opacity: 1;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background: rgba(0,0,0,1);
    display: none;
  }



  @include media-breakpoint-down(md) {
    &__body {
      &--header-open {
        overflow: hidden;
      }
    }
  }

  @include media-breakpoint-down(xss) {
    &__body--header-open{
     .page__overlay{
      display: block;
    }  
  }


   } 


}