.menu {
  width: 100%;
  display: none;  
  ul{
    font-family: 'Roboto', sans-serif;
    font-effect: 400px;
    font-style: normal;
     display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    text-align: right;
  }

  &__item {
    display: inline-block;
    vertical-align: middle;
    margin-left: 48px;
    padding: 23px 0;
    background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/0 2Px;
    transition: background 0.3s;     
    &:first-child{
      margin-left: 0;
    }
    &.active{
      background: linear-gradient(to right, #68c4c7, #68c4c7) no-repeat 0 100%/100% 2Px; 

      .menu__link {
        opacity: 1;
      }
    }
    &:hover{
      background-size: 100% 2px;
    }
  }
  &__item.active{

  }
  &__link {
    color: $color-base;
    text-decoration: none;
    text-transform: uppercase;
    opacity: 0.4;
    font-size: 14px;
    line-height: 17px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 3px 0;
    transition: 0.3s;

    &:hover {
      opacity: 1;
    }
  }



  @include media-breakpoint-up(sm) {
    display: block;
  }
}

@include media-breakpoint-down(xss) {
  .page__body--header-open .menu{
    display: block;
    position: absolute;
    width: 100%;
    top: 100%;
    margin-top: 70px;
    left: 0;
    right: 0;
    ul{
      flex-direction: column;
    }
    &__item{
      display: block;
      margin-left: 0;
      padding: 0;
      margin-bottom: 25px;
    }
  }
}