.table {
  padding-top: 0;
  &__item:not(.footable-details){
      width: 100%;
      max-width: 450px;


  thead,
  tbody{
    padding-left: 70px;
    padding-right: 70px;
  }
  span{
    color: #000000;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    display: block;
    transition: color 0.3s;
  }
  th{
    p{
    font-size: 18px; 
    font-family: 'Roboto', sans-serif;
    font-weight: 500;        
    }

  }
  tbody tr{
    cursor: pointer;
    &:hover{
      box-shadow: 0 1px 7px #ebebeb;
      border-radius: 3px;
      background-color: #ffffff;
      td span{
        color: #68c4c7; 
      }
    }
    .hover{
      border-bottom: 1px solid transparent;

    }  
  }


  td,th{
    vertical-align: top;
    padding-top: 35px;
    padding-bottom: 35px;
    padding-right: 35px;
  }
  th{
    padding-top: 15px;
    padding-bottom: 15px;
  }  
  td:not(:last-child){
    border-bottom: 1px solid #ebebeb;
  }
  th:not(:last-child){
    border-bottom: 2px solid #68c4c7;
  }
  th:first-child{
    border-bottom: 2px solid transparent;
  }
  td:first-child{
    border-bottom: 2px solid transparent;
  }
  td:first-child, th:first-child{
    padding-left: 0;
    width: 70px;
  }
  td:last-child, th:last-child{
    padding-right: 0;
    text-align: right;
    width: 70px;
  }
  td:nth-last-child(2), th:nth-last-child(2){
    text-align: right;
    padding-right: 0;
  }
  tr:last-child td{
    border: 1px solid transparent;
  }


  .title{
    color: #000000;
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
    font-weight: 500;
    line-height: 40px;
  }

  @include media-breakpoint-up(xss) {
    max-width: 940px;
    padding-bottom: 40px;
    margin: 0 auto 40px;
  }

  @include media-breakpoint-down(sm) {
    &:not(.footable-details) td, &:not(.footable-details) th{
      padding-right: 18px;
    }
  }
  }
  @include media-breakpoint-up(xss) {
    padding-bottom: 40px;
  }

}


.m-table-wrap{
    color: #000;  
    padding: 15px;
  td:first-child, th:first-child, td:last-child, th:last-child{
    display: none;
  }
  th{
    border-bottom: 0 !important;
    display: block;
    text-align: left !important;
    padding: 20px;
    color: #000;
  }
  .m-value, .m-title{
    display: inline-block;
    margin-right: 12px;
  }
  .m-title{
    text-align: left;
  }
  .m-value{
    text-align: right;
    float: right;
  }  
  .m-wrap-th{
    float: left;
    width: 50%;
  }
  .m-tr{
    float: left;
    width: 50%;
  }
  .m-db{
    display: block;
    text-align: left !important ;
    padding: 20px;
  }
}

.table__item.footable-details{
  text-align: left;
  margin-left: 80px;
  tr,td,th{
    box-shadow: 0 0.0625rem 0.4375rem transparent;
    padding: 0 15px 15px;
    text-align: left !important ;
    &:hover{
      box-shadow: 0 0.0625rem 0.4375rem transparent;

    }
  }
}

.table__item tr.footable-detail-row{

    box-shadow: 0 0.0625rem 0.4375rem transparent;
    &:hover{
      box-shadow: 0 0.0625rem 0.4375rem transparent;
    }

}

.fooicon {
  display: inline-block;
  font-size: inherit;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
}
.fooicon:before,
.fooicon:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 30px;
  padding: 15px;
}

.fooicon-plus:before {
  content: "+";
}
.fooicon-minus:before {
  content: "-";
}